/* grid.scss */

/* 기본 컨테이너 */
.container {
    width: 80%;
    margin: 0 auto; // 가운데 정렬
    padding: 0 20px; // 좌우 패딩
}

/* 유연한 그리드 시스템 */
.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.col {
    flex: 1; // 유연한 너비 설정
    padding: 0 15px; // 컬럼 내부 패딩
}

/* 지도 컨테이너 그리드 */
#map-container {
    width: 80%;
    margin: 0 auto;
    border: 2px solid #ccc;
    border-radius: 8px;
    overflow: hidden;
}

#map {
    width: 100%;
    height: 400px; // 지도 높이 설정
}

/* 섹션 스타일 */
.section {
    padding: 20px;
    margin-bottom: 20px;
}

/* 반응형 그리드 조정 */
@media screen and (max-width: 768px) {
    .container, .row, .col {
        width: 100%;
        padding: 0 10px;
    }
}
