// _header.scss
header {
    background-color: #303E4F;
    color: white;
    padding: 5px;
    position: relative;
    z-index: 10;
    padding-top: 120px; // 로고의 크기에 맞게 조절
}

.header-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    position: fixed;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    width: 100px;
    height: auto;
}
