nav {
    background-color: #666;
    color: white;
    height: 100%;
    text-align: center;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
}

.menu {
    list-style-type: none;
    padding: 0;
    background-color: #303E4F;
    z-index: 9;
}

.menu li {
    display: inline;
    margin-right: 20px;
}

.menu a {
    color: white;
    text-decoration: none;
}

.menu a:hover {
    text-decoration: underline;
}
