
.dropdown_main {
    position: relative;
    display: inline-block;
    user-select: none;
}

.dropdown_bar {
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    font-size: 24px;
    font-weight: bold;
}

.dropdown_bar svg {
    vertical-align: middle;
    margin-left: 5px;
}

.dropdown_content {
    background-color: #DAA520;
    position: absolute;
    display: none;
    width: 100%;
    top: 100%;
    left: 0;
    border-radius: 5%;
    box-shadow: 0 8px 16px rgba(0,0,0,0.2);
    z-index: 100;
}

.dropdown_content a {
    text-align: left;
    color: white;
    padding: 8px 0;
    text-decoration: none;
    display: block;
    font-size: 16px;
    width: 100%;
    box-sizing: border-box;
}

.dropdown_content li {
    margin: 0;
    padding: 10px 20px;
    list-style: none;
    line-height: 1.5;
    text-align: center;
    display: block;
}

.dropdown_main:hover .dropdown_content {
    display: block;
}
